@import url(https://fonts.googleapis.com/css2?family=Libre+Caslon+Display&family=Lobster+Two&family=Oranienbaum&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Navbar{background-color:black;width:100%;height:70px;color:white;font-family:'Oranienbaum', serif;font-size:26px;display:flex;flex-direction:row;justify-content:space-between;align-items:center}.Navbar .links{width:500px;display:flex;flex-direction:row;justify-content:space-evenly}.Navbar .name{margin-left:20px}

.Header{background-color:#b4afaf;background-image:url("https://www.transparenttextures.com/patterns/gray-floral.png");color:black;font-family:'Oranienbaum', serif;height:300px;width:100%;display:flex;flex-direction:column;justify-content:center;align-items:center}.Header__tweeter{color:black;font-family:'Oranienbaum', serif;height:300px;width:100%;display:flex;flex-direction:column;justify-content:center;align-items:center}.Header__mapbook{color:black;font-family:'Oranienbaum', serif;height:300px;width:100%;display:flex;flex-direction:column;justify-content:center;align-items:center}

.Body{display:flex;flex-direction:column}

.Portfolio{display:flex;flex-direction:row;justify-content:center;flex-wrap:wrap;width:100%;height:600px;background-color:black}.Portfolio .Portfolio__block{width:280px;height:280px;-webkit-filter:blur(5px);filter:blur(5px);opacity:0.8;display:flex;flex-direction:row;justify-content:flex-start}.Portfolio .Portfolio__block:hover{-webkit-filter:none;filter:none;opacity:1;transition:-webkit-filter 0.3s;transition:filter 0.3s;transition:filter 0.3s, -webkit-filter 0.3s;transition:opacity 0.3s}

.App{display:flex;flex-direction:column;text-align:center;font-size:16px}

